@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap");

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #feeae8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Quicksand";
  color: #333;
}

.App {
  margin: auto;
  width: 70vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container {
  display: inline-block;
  width: 100%;
}

.score {
  width: 70%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.score span {
  font-size: 1.5em;
  margin: 10px;
}

.bottomButtons {
  width: 70%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.bottomButtons button {
  padding: 10px;
  margin: 0 10px;
} 

.colourBlocksContainer {
  display: flex;
  flex-direction: row;
  height: 20vw;
  margin: 1vh auto 1vh auto;
  
}

.colourBlocksContainer div {
  display: flex;
  margin: 0.5%;
  width: 100%;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
}

.colourBlocksContainer div h3 {
  color:black;
  font-size: 1.5em;
}

.answerButtonsContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 0 auto;
}

.answerButtonsContainer button {
  width: 100%;
  padding: 1.3%;
  margin: 1%;
  transition: all 0.3s ease;
  font-size: 1.3em;
}

h1 {
  text-align: center;
  font-size: 3em;
}

h2 {
  font-size: 2em;
  text-align: center;
  margin: 2vh 0;
}

.header {
  border-bottom: 1px solid grey;
  width: 100%;
  margin: 2vh auto;
}

@media only screen and (max-width: 1000px) {
  .App {
    width: 90vw;
  }
  h1 {
    font-size: 2.5em;
  }
  header {
    width: 90%;
  }
  .colour-blocks{
    height:25vh
  }
}


@media only screen and (max-width: 800px) {
  .App {
    width: 100vw;
  }
  .colour-blocks{
    height:30vh
  }
}

@media (hover: hover) {
  button:hover {
    box-shadow: 2px 3px 4px 0px rgb(0 0 0 / 36%);
  }
}